import { useState, useRef, useCallback, useEffect } from 'react';
import { useToast } from '../components/ToastContext';
import { ElectronAPI } from '../types/electron';

const MAX_LOGS_TO_LOAD = 100; //    

//   
interface LogEntry {
  id: number;
  content: string;
  key_count: number;
  typing_time: number;
  timestamp: string;
  created_at: string;
  is_saved?: boolean;
  window_title?: string;
  browser_name?: string;
  total_chars?: number;
  total_chars_no_space?: number;
  total_words?: number;
  pages?: number;
  accuracy?: number;
}

//     
interface TypingStatsUpdate {
  keyCount: number;
  typingTime: number;
  windowTitle?: string;
  browserName?: string;
  totalChars?: number;
  totalCharsNoSpace?: number;
  totalWords?: number;
  pages?: number;
  accuracy?: number;
}

//     
interface RecordData {
  content: string;
  keyCount: number;
  typingTime: string;
  timestamp: string;
  windowTitle?: string;
  browserName?: string;
  totalChars?: number;
  totalWords?: number;
  pages?: number;
  accuracy?: number;
}

export interface TypingStatsState {
  keyCount: number;
  typingTime: number;
  windowTitle: string;
  browserName: string;
  totalChars: number;
  totalCharsNoSpace: number;
  totalWords: number;
  pages: number;
  accuracy: number;
}

export function useTypingStats(electronAPI: ElectronAPI | null) {
  // API   
  const [logs, setLogs] = useState<LogEntry[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isTracking, setIsTracking] = useState(false);
  const [error, setError] = useState<string | null>(null);
  
  //    ref 
  const eventsCleanupRef = useRef<(() => void)[]>([]);
  const intervalsRef = useRef<NodeJS.Timeout[]>([]);
  
  //    - useRef    
  const currentStatsRef = useRef<TypingStatsState>({
    keyCount: 0,
    typingTime: 0,
    windowTitle: '',
    browserName: '',
    totalChars: 0,
    totalCharsNoSpace: 0,
    totalWords: 0,
    pages: 0,
    accuracy: 100
  });
  
  //    useState 
  const [displayStats, setDisplayStats] = useState({...currentStatsRef.current});
  const { showToast } = useToast();
  
  //     (  )
  useEffect(() => {
    const updateInterval = setInterval(() => {
      //     
      if (JSON.stringify(currentStatsRef.current) !== JSON.stringify(displayStats)) {
        setDisplayStats({...currentStatsRef.current});
      }
    }, 2000); // 2 
    
    intervalsRef.current.push(updateInterval);
    
    return () => clearInterval(updateInterval);
  }, [displayStats]);

  //   
  const handleStartTracking = useCallback(() => {
    try {
      if (electronAPI) {
        electronAPI.startTracking?.();
        setIsTracking(true);
      } else {
        console.warn('electronAPI .');
        setIsTracking(true); // API  UI tracking  
      }
    } catch (error) {
      console.error('startTracking  :', error);
      setIsTracking(true);
    }
  }, [electronAPI]);

  //   
  const handleStopTracking = useCallback(() => {
    try {
      if (electronAPI) {
        electronAPI.stopTracking?.();
        setIsTracking(false);
      } else {
        console.warn('electronAPI .');
        setIsTracking(false);
      }
    } catch (error) {
      console.error('stopTracking  :', error);
      setIsTracking(false);
    }
  }, [electronAPI]);

  //    
  const fetchLogs = useCallback(async (limit = MAX_LOGS_TO_LOAD) => {
    if (isLoading) return; //      
    
    try {
      setIsLoading(true);
      const endpoint = `/api/getLogs?limit=${limit}`;
      const response = await fetch(endpoint);
      
      if (!response.ok) {
        throw new Error(`API  : ${response.status} ${response.statusText}`);
      }
      
      const contentType = response.headers.get('content-type');
      if (!contentType || !contentType.includes('application/json')) {
        throw new Error('    ');
      }
      
      const data = await response.json();
      
      if (data.success) {
        //     
        const optimizedLogs = data.logs.map((log: any) => ({
          id: log.id,
          content: log.content,
          key_count: log.key_count,
          typing_time: log.typing_time,
          timestamp: log.timestamp,
          created_at: log.created_at,
          window_title: log.window_title,
          browser_name: log.browser_name,
          total_chars: log.total_chars,
          total_words: log.total_words,
          pages: log.pages,
          accuracy: log.accuracy
        }));
        
        //    
        if (JSON.stringify(logs) !== JSON.stringify(optimizedLogs)) {
          setLogs(optimizedLogs);
        }
      } else {
        console.error('  :', data.error);
        setLogs([]);
      }
    } catch (error) {
      console.error(' API  :', error);
      setLogs([]);
      setError('    .');
      showToast?.('    ', 'error');
    } finally {
      setIsLoading(false);
    }
  }, [logs, showToast, isLoading]);

  //   
  const saveToDatabase = useCallback(async (record: RecordData) => {
    try {
      const response = await fetch('/api/saveLogs', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(record)
      });

      const result = await response.json();
      
      if (response.ok) {
        await fetchLogs(); //   
      } else {
        console.error(' :', result.error);
      }
    } catch (error) {
      console.error(' API  :', error);
    }
  }, [fetchLogs]);

  //   
  const handleSaveStats = useCallback((content: string) => {
    try {
      if (electronAPI) {
        electronAPI.saveStats?.(content);
      }
      
      // DB 
      const recordData: RecordData = {
        content,
        keyCount: currentStatsRef.current.keyCount,
        typingTime: currentStatsRef.current.typingTime.toString(), // number string 
        timestamp: new Date().toISOString(),
        windowTitle: currentStatsRef.current.windowTitle,
        browserName: currentStatsRef.current.browserName,
        totalChars: currentStatsRef.current.totalChars,
        totalWords: currentStatsRef.current.totalWords,
        pages: currentStatsRef.current.pages,
        accuracy: currentStatsRef.current.accuracy
      };
      
      saveToDatabase(recordData);
    } catch (error) {
      console.error('saveStats  :', error);
      // API     
      const recordData: RecordData = {
        content,
        keyCount: currentStatsRef.current.keyCount,
        typingTime: currentStatsRef.current.typingTime.toString(), // number string 
        timestamp: new Date().toISOString(),
        windowTitle: currentStatsRef.current.windowTitle,
        browserName: currentStatsRef.current.browserName || 'Unknown'
      };
      
      saveToDatabase(recordData);
    }
  }, [electronAPI, saveToDatabase]);

  //  
  const initializeEventListeners = useCallback(() => {
    if (!electronAPI) return;
    
    //       
    const cleanupFunctions: (() => void)[] = [];
    
    //     
    if (electronAPI.onTypingStatsUpdate) {
      const cleanup = electronAPI.onTypingStatsUpdate((data: TypingStatsUpdate) => {
        //    
        currentStatsRef.current = {
          ...currentStatsRef.current,
          ...data
        };
      });
      
      cleanupFunctions.push(cleanup);
    }
    
    //    
    if (electronAPI.onStatsSaved) {
      const cleanup = electronAPI.onStatsSaved(() => {
        //    
        fetchLogs();
      });
      
      cleanupFunctions.push(cleanup);
    }
    
    //   ref 
    eventsCleanupRef.current = cleanupFunctions;
  }, [electronAPI, fetchLogs]);

  //      
  useEffect(() => {
    initializeEventListeners();
    
    //      
    return () => {
      eventsCleanupRef.current.forEach(cleanup => {
        try {
          cleanup();
        } catch (error) {
          console.error('   :', error);
        }
      });
    };
  }, [initializeEventListeners]);

  //     
  useEffect(() => {
    return () => {
      //     
      eventsCleanupRef.current.forEach(cleanup => cleanup());
      eventsCleanupRef.current = [];
      
      //    
      intervalsRef.current.forEach(clearInterval);
      intervalsRef.current = [];
      
      //    
      setLogs([]);
      currentStatsRef.current = {
        keyCount: 0,
        typingTime: 0,
        windowTitle: '',
        browserName: '',
        totalChars: 0,
        totalCharsNoSpace: 0,
        totalWords: 0,
        pages: 0,
        accuracy: 100
      };
      
      //   
      if (window.gc) {
        try {
          window.gc();
        } catch (e) {
          console.log('GC  ');
        }
      }
    };
  }, []);

  //     
  useEffect(() => {
    fetchLogs();
  }, [fetchLogs]);

  return {
    logs,
    isLoading, 
    isTracking,
    displayStats,
    handleStartTracking,
    handleStopTracking,
    handleSaveStats,
    fetchLogs,
    currentStatsRef,
    error
  };
}
