/**
 *    
 */

//    
export enum OptimizationLevel {
  Normal = 0,
  Low = 1,
  Medium = 2,
  High = 3,
  Critical = 4
}

// GPU   
export interface GpuDeviceInfo {
  name: string;
  vendor: string;
  driver_info: string;
  device_type: string;
  backend: string;
  timestamp: number;
}

// GPU  
export interface GpuInfo {
  name: string;
  vendor: string;
  driver_info: string;
  device_type: string;
  backend: string;
  available: boolean;
  timestamp: number;
}

// GPU   
export interface GpuTaskResult {
  task_id: string;
  task_type: string;
  duration_ms: number;
  result: any;
  timestamp: number;
}

//   
export interface TaskResult {
  task_id: string;
  task_type: string;
  duration_ms: number;
  result: any;
  timestamp: number;
}

// GPU   
export interface GpuComputationResult {
  computation_type: string;
  duration_ms: number;
  result: any;
  timestamp: number;
}

//  GPU  (Rust enum )
export enum GPUType {
  Unknown = 0,
  Integrated = 1,
  Discrete = 2,
  Software = 3
}

//  GPU   (Rust struct )
export interface GPUInfo {
  name: string;
  vendor: string;
  device_type: GPUType;
  available: boolean;
  acceleration_enabled: boolean;
  renderer?: string;
  driver_info?: string;
  backend?: string;
  timestamp: number;
}

//    
export interface WorkerPoolStats {
  thread_count: number;
  active_tasks: number;
  completed_tasks: number;
  active_workers: number;
  idle_workers: number;
  pending_tasks: number;
  failed_tasks: number;
  total_tasks: number;
  uptime_ms: number;
  timestamp: number;
}

//   
export interface PerformanceMetrics {
  calls: number;
  errors: number;
  avgExecutionTime: number;
  lastGcTime: number;
  totalExecutionTime: number;
  timestamp: number;
}

//    
export interface NativeModuleStatus {
  available: boolean;
  fallbackMode: boolean;
  version: string | null;
  info: any;
  timestamp: number;
  error?: string;
}

// GPU   
export enum GpuTaskType {
  MatrixMultiplication = 'matrix',
  TextAnalysis = 'text',
  ImageProcessing = 'image',
  DataAggregation = 'data',
  PatternDetection = 'pattern',
  TypingStatistics = 'typing',
  Custom = 'custom'
}

// GPU   
export interface GpuAccelerationStatus {
  available: boolean;
  enabled: boolean;
  info?: GpuInfo;
}

//   
export interface MemoryInfo {
  totalMemory: number;
  freeMemory: number;
  usedMemory: number;
  memoryUsagePercent: number;
  usageLevel: 'LOW' | 'MEDIUM' | 'HIGH' | 'CRITICAL';
}

//   
export interface OptimizationResult {
  success: boolean;
  memoryBefore?: number;
  memoryAfter?: number;
  memoryFreed?: number;
  duration?: number;
  error?: string;
}

//    
export interface GCResult {
  success: boolean;
  freed_memory: number;
  freed_mb?: number;
  timestamp: number;
  error?: string;
}

/**
 *   
 */
export interface NativeMemoryModule {
  /**
   *   
   */
  getMemoryStatus(): Promise<MemoryInfo>;
  
  /**
   *   
   * @param level   (1-10)
   */
  optimizeMemory(level?: number): Promise<OptimizationResult>;
  
  /**
   *   
   */
  forceGarbageCollection(): Promise<OptimizationResult>;
  
  /**
   *     
   */
  isNativeImplementationAvailable(): Promise<boolean>;
}

export interface NativeGPUModule {
  /**
   * GPU  
   * @param enabled  
   */
  setGpuAcceleration(enabled: boolean): Promise<boolean>;
  
  /**
   * GPU  
   */
  getGpuInfo(): Promise<{
    available: boolean;
    vendor?: string;
    renderer?: string;
    version?: string;
    memorySize?: number;
    isHardwareAccelerated: boolean;
  }>;
  
  /**
   *     
   */
  isHardwareAccelerationEnabled(): Promise<boolean>;
}

export interface NativeUtils {
  /**
   *   
   */
  getEnvironmentInfo(): Promise<{
    platform: string;
    architecture: string;
    cpuCores: number;
    totalMemory: number;
    freeMemory: number;
  }>;
  
  /**
   *    
   * @param callback  
   */
  measurePerformance<T>(callback: () => T): Promise<{
    result: T;
    duration: number;
  }>;
}

export interface NativeModules {
  memory: NativeMemoryModule;
  gpu: NativeGPUModule;
  utils: NativeUtils;
}
