import { useState, useEffect } from 'react';
import { ElectronAPI } from '../types/electron';

//   API  
const createDummyElectronAPI = (): ElectronAPI => ({
  windowControl: (action) => console.log(` windowControl : ${action}`),
  onTypingStatsUpdate: (_callback) => {
    console.log(' onTypingStatsUpdate  ');
    return () => console.log(' onTypingStatsUpdate  ');
  },
  onStatsSaved: (_callback) => {
    console.log(' onStatsSaved  ');
    return () => console.log(' onStatsSaved  ');
  },
  startTracking: () => console.log(' startTracking '),
  stopTracking: () => console.log(' stopTracking '),
  saveStats: (data?) => {
    console.log(' saveStats :', data);
    return Promise.resolve(true);
  },
  loadSettings: () => {
    console.log(' loadSettings ');
    return { darkMode: false, windowMode: 'normal' };
  },
  saveSettings: (settings) => {
    console.log(' saveSettings :', settings);
    return true;
  },
  getCurrentBrowserInfo: () => Promise.resolve({ name: null, isGoogleDocs: false, title: null }),
  getDebugInfo: () => Promise.resolve({
    isTracking: false,
    currentStats: {
      keyCount: 0,
      typingTime: 0,
      startTime: null,
      lastActiveTime: null,
      currentWindow: null,
      currentBrowser: null,
      totalChars: 0,
      totalWords: 0,
      totalCharsNoSpace: 0,
      pages: 0,
      accuracy: 100
    },
    platform: 'web',
    electronVersion: 'N/A',
    nodeVersion: 'N/A'
  }),
  setDarkMode: () => Promise.resolve({ success: true }),
  setWindowMode: () => Promise.resolve({ success: true }),
  getWindowMode: () => Promise.resolve('windowed' as WindowModeType),
  checkAutoStart: () => {},
  onAutoTrackingStarted: () => () => {},
  onSwitchTab: () => () => {},
  onOpenSaveStatsDialog: () => () => {},
  requestStatsUpdate: () => {},
  onMiniViewStatsUpdate: () => () => {},
  toggleMiniView: () => {},
  updateTraySettings: () => Promise.resolve({ success: true }),
  quitApp: () => console.log(' quitApp '),
  toggleWindow: () => console.log(' toggleWindow '),
  onBackgroundModeChange: () => () => {},
  onTrayCommand: () => () => {},
  restartApp: () => console.log(' restartApp '),
  showRestartPrompt: () => console.log(' showRestartPrompt '),
  closeWindow: () => console.log(' closeWindow '),
  getDarkMode: () => Promise.resolve(false)
});

export function useElectronApi() {
  const [api, setApi] = useState<ElectronAPI | null>(null);

  useEffect(() => {
    //   
    if (typeof window !== 'undefined') {
      // window  electronAPI  
      const electronAPI = (window as any).electronAPI as ElectronAPI | undefined;
      
      if (electronAPI) {
        // Electron   
        setApi(electronAPI);
      } else {
        //      API 
        setApi(createDummyElectronAPI());
      }
    }
  }, []);

  return { 
    electronAPI: api,
    api, //   alias 
    isElectron: typeof (window as any).electronAPI !== 'undefined'
  };
}
