/**
 * GPU   
 * @param taskType  
 * @param params  
 * @returns  
 */
export const performGpuComputation = async (
  taskType: GpuTaskType,
  params: Record<string, any>
): Promise<any | null> => {
  try {
    //    GPU  
    const response = await window.nativeModule.performGpuComputation(taskType, params);

    if (!response.success || !response.data) {
      console.error(`GPU (${taskType}) :`, response.error);
      return null;
    }

    return response.data;
  } catch (error) {
    console.error(`GPU (${taskType})   :`, error);
    return null;
  }
};

/**
 * GPU  
 * @returns GPU 
 */
export const getGpuInfo = async (): Promise<GpuInfo | null> => {
  try {
    //    GPU  
    const response = await window.nativeModule.getGpuInfo();

    if (!response.success || !response.data) {
      console.error('GPU    :', response.error);
      return null;
    }

    return response.data;
  } catch (error) {
    console.error('GPU     :', error);
    return null;
  }
};

/**
 * GPU  
 * @param enabled  
 * @returns  
 */
export const setGpuAcceleration = async (enabled: boolean): Promise<boolean> => {
  try {
    //    GPU  
    const response = await window.nativeModule.setGpuAcceleration(enabled);

    if (!response.success) {
      console.error('GPU   :', response.error);
      return false;
    }

    return response.data || false;
  } catch (error) {
    console.error('GPU     :', error);
    return false;
  }
};

/**
 *    
 * @returns    
 */
export async function getNativeModuleStatus(): Promise<NativeModuleStatus> {
  //    
  if (moduleStatusCache && Date.now() - (moduleStatusCache.timestamp || 0) < 60000) {
    return moduleStatusCache;
  }

  try {
    // GPU      
    const gpuInfo = await getGpuInfo();

    //      
    if (gpuInfo) {
      // NativeResponse  NativeModuleStatus  
      const result: NativeModuleStatus = {
        available: true,
        fallbackMode: false,
        version: '1.0.0', //   
        info: gpuInfo,
        timestamp: Date.now(),
      };

      //  
      moduleStatusCache = result;
      return result;
    }

    //       
    const errorStatus: NativeModuleStatus = {
      available: false,
      fallbackMode: true,
      version: null,
      info: null,
      timestamp: Date.now(),
      error: '    .',
    };

    moduleStatusCache = errorStatus;
    return errorStatus;
  } catch (error) {
    console.error('    :', error);

    const errorStatus: NativeModuleStatus = {
      available: false,
      fallbackMode: true,
      version: null,
      info: null,
      timestamp: Date.now(),
      error: error instanceof Error ? error.message : '   ',
    };

    moduleStatusCache = errorStatus;
    return errorStatus;
  }
}

/**
 *      
 * @returns   
 */
export function isNativeModuleAvailable(): boolean {
  return typeof window !== 'undefined' && !!window.nativeModule;
}
