'use client';

import React from 'react';
import ConfirmDialog from './ConfirmDialog';

interface SaveConfirmDialogProps {
  isOpen: boolean;
  onConfirm: () => void;
  onCancel: () => void;
  settingsType?: string;
  isDarkMode?: boolean;
}

/**
 *    
 */
const SaveConfirmDialog: React.FC<SaveConfirmDialogProps> = ({
  isOpen,
  onConfirm,
  onCancel,
  settingsType = '',
  isDarkMode = false
}) => {
  const title = `${settingsType} `;
  const message = ` ${settingsType}() ?`;

  return (
    <ConfirmDialog
      title={title}
      message={message}
      isOpen={isOpen}
      onConfirm={onConfirm}
      onCancel={onCancel}
      confirmText=""
      cancelText=""
      type="info"
      isDarkMode={isDarkMode}
    />
  );
};

export default SaveConfirmDialog;
